/* axios  */
import store from '@/store'
import axios from 'axios'
import {
  Toast
} from 'vant'
let getTokenCount = 0
// 
const headers = {
  'Content-Type': 'application/json'
}
//  axios 
const instance = axios.create({
  // 
  timeout: 4000,
  // 
  headers
})
instance.interceptors.request.use(
  request => {
    store.commit('updateLoadingStatus', {
      isLoading: true
    })
    request.headers.Token = store.state.loginRegister.token
    return request
  },
  error => {
    console.log(error)
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
  }
)
/**
 * 
 */
instance.interceptors.response.use(
  response => {
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
    let data = response.data
    console.log(data)
    switch (data.Code || data.code) {
      case 1:
        getTokenCount = 0
        return data
      case 2:
        getTokenCount = 0
        var requestUrl = response.request.responseURL.split('/')
        if (requestUrl[requestUrl.length - 1] === 'Login') {
          return data
        }
        Toast(data.message || data.Message)
        return Promise.reject(response)
      case -6:
      case -7:
      case -8:
      case -9:
        // token   -1 
        response.status = '-1'
        return Promise.reject(response)
      default:
        return Promise.reject(response)
    }
  },
  error => {
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
    if (error.response) {
      switch (error.response.status) {
        case 400:
          error.message = ''
          break
        case 401:
          error.message = ''
          break
        case 403:
          error.message = ''
          break
        case 404:
          error.message = `: ${error.response.config.url}`
          break
        case 408:
          error.message = ''
          break
        case 500:
          error.message = ''
          break
        case 501:
          error.message = ''
          break
        case 502:
          error.message = ''
          break
        case 503:
          error.message = ''
          break
        case 504:
          error.message = ''
          break
        case 505:
          error.message = 'HTTP'
          break
        default:
          error.message = ''
      }
    }
    Toast(error.message)
    return Promise.reject(error)
  }
)

instance.interceptors.response.use(
  undefined,
  error => {
    if (error.status === '-1' && getTokenCount <= 4) {
      let UserName = store.state.loginRegister.username
      let Password = store.state.loginRegister.password
      if (UserName && Password) {
        getTokenCount++
        return new Promise((resolve) => {
          store.dispatch('getUserToken', {
            UserName,
            Password
          }).then(() => {
            resolve(instance(error.config))
          }).catch(() => {
            logOut()
          })
        })
      } else {
        if (error.data && error.data.message) {
          Toast(error.data.message)
        }
        logOut()
      }
    } else {
      if (error.data && (error.data.message || error.data.Message)) {
        Toast(error.data.message || error.data.Message)
      }
      return Promise.reject(error)
    }
  }
)

//  request  post get 
const request = {
  /*
   * post 
   * @url 
   * @data 
   */
  post(url, data) {
    //  
    console.log(`url: ${url} , data: ${JSON.stringify(data || {})}`)
    return instance({
      method: 'post',
      url: '/api/ft/' + url,
      data: JSON.stringify(data || {}) || {}
    })
  }
}
export {
  request
}

// 
function logOut() {
  store.dispatch('logout').then(() => {
    location.reload()
  })
}
